// import $ from 'jquery';

export default function MainCtrl() {
  var vm = this;
  // 
  vm.routes_c = [
    {
      url: 'zhxx.company',
      text: '',
      active: false
    },
    {
      url: 'gsfu',
      text: '',
      active: false
    },
    {
      url: 'fwlb',
      text: '',
      active: false
    },
    {
      url: 'gsal',
      text: '',
      active: false
    },
    {
      url: 'allb',
      text: '',
      active: false
    },
    {
      url: 'qyzy.qyzz',
      text: '',
      active: false
    },
    {
      url: 'yqlj',
      text: '',
      active: false
    },
    {
      url: 'ztxg',
      text: '',
      active: false
    },
    {
      url: 'email.inbox',
      text: '',
      active: false
    },
    {
      url: 'kjrz',
      text: '',
      active: false
    },
    {
      url: 'kjrzlist',
      text: '',
      active: false
    },
    {
      url: 'gsdt',
      text: '',
      active: false
    }
  ];

  vm.routes_p = [
    {
      url: 'pzhxx.person',
      text: '',
      active: false
    },
    {
      url: 'email.inbox',
      text: '',
      active: false
    },
    // {
    //   url: 'addresslist',
    //   text: '',
    //   active: false
    // },

  ];

  let cookies = {};
  document.cookie.split('; ').forEach(item => {
    let arr = item.split('=');
    cookies[arr[0]] = arr[1];
  })
  if (cookies.accountType) {
    vm.accountType = cookies.accountType;
    vm.logintype = cookies.logintype;
  }


  vm.routes = {
    title: '',
    items: vm.logintype == 1 ? vm.routes_p : vm.routes_c
  };
}



